.popup {
  display: flex;
  border-radius: 4px;
  position: fixed;
  width: 1000px;
  height: 600px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  /* background-color: rgba(0,0,0, 0.5); */
}
.popup_inner {
  
  border-radius: 50px;
  
  left: 25%;
  right: 25%;
  height: 350px;
  top: 25%;
  bottom: 25%;
  margin: auto;
  background: rgba(117, 211, 10, 0.5);
  border: 2px solid rgb(111, 0, 255);
}


