.form-group input[type=text]:focus, .form-group input[type=password]:focus {
  background-color: #ddd;
  outline: none;
}

.form-group input[type=text], .form-group input[type=password] {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  border: none;
  background: #f1f1f1;
}
.form-group {
  margin: 0 370px;
  border: 2px solid rgb(111, 0, 255);
  border-radius: 4px;
  max-width: 300px;
